import React from 'react';
import ReactDOM from 'react-dom';
import './scheme.css';
import Scheme0 from './Scheme0';
import Scheme1 from './Scheme1';
import Scheme2 from './Scheme2';

export default class Scheme extends React.Component {
  constructor() {
    super();
    this.state = {
      mouseIsDown: false,
      translateTransform: {x: 0, y: 0} ,
      prevMousePosition: { x: 0, y: 0 },
      prevTranslateTransform: { x: 0, y: 0 },
      scaleTransform: 1,
      prevSearch: null
    };
  }
  scrollToEl = () => {
      const node = ReactDOM.findDOMNode(this);
      window.scroll(0, node.offsetTop);
  }
  componentDidMount() {
    if (window.location.search) {
      let urlParams = new URLSearchParams(window.location.search);
      let e = { target: { id: urlParams.get('current') }};
      this.props.selectCurrentCompany(e);
    }

  }
  componentDidUpdate() {
    if (window.location.search) {
      if (this.state.prevSearch !== window.location.search){
        console.log(this.state.prevSearch);
        console.log(window.location.search);
        setTimeout(() => {
          this.scrollToEl()
        } 
        , 100);
        this.setState({prevSearch: window.location.search})
      }
    }
  }

  onMouseMove = (e) => {
    if (this.state.mouseIsDown) {
      const dragX = e.clientX;
      const dragY = e.clientY;
      const prevX = this.state.prevMousePosition.x;
      const prevY = this.state.prevMousePosition.y;

      this.setState(prevState => ({
        translateTransform: {
          x: dragX - prevX + this.state.prevTranslateTransform.x,
          y: dragY - prevY + this.state.prevTranslateTransform.y
        } 
      }))
    }
  }

  startDrag = (e) => {
    this.setState({ 
      mouseIsDown: true,
      prevMousePosition: {
        x: e.clientX,
        y: e.clientY
      },
      prevTranslateTransform: Object.assign({}, this.state.translateTransform)
    });
  }

  endDrag = (e) => {
    if (this.state.mouseIsDown) {
      this.setState({
        mouseIsDown: false,
      })
    }
  }

  zoomIn = (e) => {
    if (this.state.scaleTransform >= 2) {
      return;
    }
    this.setState(prevState => ({
      scaleTransform: prevState.scaleTransform + 0.2
    }));
  }

  zoomOut = (e) => {
    if (this.state.scaleTransform <= 0.8) {
      return;
    }
    this.setState(prevState => ({
      scaleTransform: prevState.scaleTransform - 0.2
    }))
  }

  render() {
    const { children, currentLevel } = this.props;
    const { translateTransform, scaleTransform } = this.state;
    return (
      <div className="scheme-container">
        <div className="scale-btn-container">
          <button onClick={this.zoomIn} className="scale-btn-plus">
            <img src={require('../../assets/icons/add.svg')} alt="zoomIn btn"></img>
          </button>
          <button onClick={this.zoomOut} className="scale-btn-minus">
            <img src={require('../../assets/icons/remove.svg')} alt="zoomOut btn"></img>
          </button>
        </div>
        <div class="scheme">
          {children}
          <div
          onMouseDown={this.startDrag}
          onMouseUp={this.endDrag}
          onMouseLeave={this.endDrag}
          onMouseMove={this.onMouseMove}
          >
            {
              currentLevel === '0' && 
              <Scheme0 {...this.props} 
                translateTransform={translateTransform}
                scaleTransform={scaleTransform}
              />
              }
            {
              currentLevel === '1' && 
              <Scheme1 {...this.props} 
                translateTransform={translateTransform}
                scaleTransform={scaleTransform}
              />
              }
            {
              currentLevel === '2' && 
              <Scheme2 {...this.props} 
                translateTransform={translateTransform}
                scaleTransform={scaleTransform}
              />
            }
          </div>
        </div>
      </div>
    );
  }
}