import React, { Component } from 'react';
import './Footer.css';

class Footer extends Component {
  render() {
    return (
      <div className="footer">
        <h3 className="footer-header"> </h3>
        <div className="footer-item">
          <img src={require('../../assets/icons/escalator.svg')} alt="escalator icon"></img>
          <p></p>
        </div>
        <div className="footer-item">
          <img src={require('../../assets/icons/toilet.svg')} alt="WC icon"></img>
          <p>WC</p>
        </div>
        <div className="footer-item">
          <img src={require('../../assets/icons/lift.svg')} alt="lift icon"></img>
          <p></p>
        </div>
        <div className="footer-item">
          <img src={require('../../assets/icons/parking.svg')} alt="parking icon"></img>
          <p></p>
        </div>
        <div className="footer-item">
          <img src={require('../../assets/icons/stairs.svg')} alt="stairs icon"></img>
          <p></p>
        </div>
      </div>
    );
  }
}

export default Footer;
 