import React, { Component } from 'react';
import Select from 'react-select';


class CategoriesFilter extends Component {
  constructor(props) {
    super(props);
    this.state = { options: this.getOptions() };
  }

  getOptions() {
    const { categories, subcategories } = this.props;
   
    const opt = categories.map(category => 
      [
          { value: category.id, label: category.name, isDisabled: true},
          ...subcategories.filter(subcategory => subcategory.category_id === category.id)
          .map(subcategory => (
             { value: subcategory.id, label: subcategory.name }
          ))
      ]
    );
    let opt2 = [];
    for (let i in opt) {
      opt2 = [...opt2, ...opt[i]];
    }
    return opt2;
  }
 
  render() {
    const { onChange, styles } = this.props;
    const options = this.getOptions();
    return (
      <Select 
        options={options} 
        isMulti={true} 
        onChange={value => onChange(value)} 
        placeholder="  " 
        styles={styles}
      />
    );
  }
}

export default CategoriesFilter;
